.todo-completed {
	border-top: 1px solid #e3e3e3;

	.todo-list {
		flex-direction: column;
		padding: 0px 20px 20px;
	}
}

.todo-completed__title {
	display: flex;
	align-items: center;
	gap: 10px;
	padding: 20px 20px 20px 30px;
	cursor: pointer;
	transition: background-color 0.3s ease 0s;

	&:hover {
		background-color: #222327;
	}
}
